
import React, { Component } from 'react';  
import tableau from 'tableau-api';  
  
  
class Viz3 extends Component {  
  componentDidMount() {  
    this.initViz()  
  }  
  
  
  initViz() {  
    const vizUrl = 'https://public.tableau.com/views/Bestrating/Sheet1?:display_count=y&publish=yes&:origin=viz_share_link';  
    const vizContainer = this.vizContainer;  
    let viz = new window.tableau.Viz(vizContainer, vizUrl)  
  }  
  
  
  render() {  
    return (  
      <div style={{height:500, width:"100%"}} ref={(div) => { this.vizContainer = div }}>  
      </div>  
    )  
  }  
}  
  
  
export default Viz3;  