
import React, { Component } from 'react';   
 import Viz3 from './Viz3';
  import Card from 'react-bootstrap/Card'; 
class Search extends Component {  
  componentDidMount() {  
     
  }  
  
   
  
  
  render() {  
    return (  
      <div style={{position:'absolute',top:50,bottom:0,right:0,left:0, overflow:'auto', padding:20,
    backgroundImage:'linear-gradient(to bottom right, #aa4b6b, #3b8d99)'}}>  
      <Card>
      <Card.Header><h6>Questions</h6></Card.Header>
      <Card.Body>
      <ul>
      <li>Initial Table:
      <ul>
      <li>What are the best rated books?</li>
      <li>What are the best rated books in a given year range?</li>
      <li>What are the best rated books for a selected author or authors?</li>
      <li>Search for a book with title</li>
      </ul>
      </li>
      <li>Readers for selected book/books:
      <ul>
      <li>Who are the users reading the book?</li>
      <li>Who are the users reading multiple selected books?</li>
      </ul>
      </li>
      <li>Shelves for selected book/books:
      <ul>
      <li>To which shelf does the book/books belong?</li>
      <li>How many times is any book placed in any particular shelf?</li>
      <li>Most common shelves the book belongs to?</li>
      </ul>
      </li>
      </ul>
      </Card.Body>
      </Card>
      <br/>
      <Card>
      <Card.Body>
      <Viz3 />
      </Card.Body>
      </Card>
      </div>  
    )  
  }  
}  
  
  
export default Search;  