
import React, { Component } from 'react';   
import Viz2 from './Viz2'; 
import Card from 'react-bootstrap/Card';  

class Genre extends Component {  
  componentDidMount() {  
     
  }  
  
   
  
  
  render() {  
    return (  
      <div style={{position:'absolute',top:50,bottom:0,right:0,left:0, overflow:'auto', padding:20,
    backgroundImage:'linear-gradient(to bottom right, #aa4b6b, #3b8d99)'}}>  
    <div style={{display: 'flex',  justifyContent:'space-between', alignItems:'top'}}>
      <Card style={{width:'49.5%'}}>
      <Card.Body>
      
    The Genre cluster is a view for books grouped by their genre. It also visualizes the relative influence of books in comparison from all genres of books or those in the selected genre(s). The plot has  two attributes associated: 
<ol>
<li>The color of the circle gives the genre to which the belongs to.</li>
<li>The size of the circle gives total ratings for the book.</li>
</ol>
      
      </Card.Body>
      </Card>
      
      <Card style={{width:'49.5%'}}>
      <Card.Body>
      On selecting genre(s) the books are filtered for chosen genre giving a different view.
      It is a word cloud representing the most rated books categorized by genre(color).
      For each book, tooltip gives all information along with the count for each 'star' rating.
      Context menu gives a link to reach the GoodReads website
      </Card.Body>
      </Card> 
      </div>
      <br/>
      <Card>
      <Card.Header><h6>Questions</h6></Card.Header>
      <Card.Body>
      <ul>
      <li>Trends:
      <ul>
      <li>Which are the most rated Genres?</li>
      <li>Which are the most available Genres?</li>
      </ul>
      </li>
      <li>For a selected Genre/Genres:
      <ul>
      <li>Which are the most rated books?</li>
      <li>To which Genre among selected, does the most rated books belong?</li>
      <li>What are the individual properties of books like ratings?</li>
      <li>Link to books?</li>
      </ul>
      </li>
      </ul>
      </Card.Body>
      </Card>
      <br/>
      <Card>
      <Card.Body>
      <Viz2 />
      </Card.Body>
      </Card>
      </div>  
    )  
  }  
}  
  
  
export default Genre;  