import React from 'react';
import logo from './logo.svg';
import './App.css';
import { AppBar } from '@material-ui/core';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import ToggleButton from 'react-bootstrap/ToggleButton'
import ToggleButtonGroup from 'react-bootstrap/ToggleButtonGroup';
import ButtonToolbar from 'react-bootstrap/ButtonToolbar';
import Button from 'react-bootstrap/Button'
import 'bootstrap/dist/css/bootstrap.min.css';

import Home from './Home';
import Author from './Author';
import Genre from './Genre';
import Network from './Network';
import Recommendation from './Recommendation';
import Search from './Search';
import Student from './Student';

class App extends React.Component {
constructor(props) {
	super(props);
    this.state = {selected: 'home'};
}
selecttab = (val) => {
	var state = this.state;
	state.selected = val;
	this.setState(state);
}
render() {
	const {selected} = this.state;
  return (
  	<div>
    <div className="App">
      <AppBar position="static">
  	<Toolbar variant="dense" style={{height:50}}>		
    
    <div >
     	 <ButtonToolbar >
    <ToggleButtonGroup type="radio" name="options" defaultValue={selected} onChange={val => this.selecttab(val)}>
      <ToggleButton value={'home'}>Home</ToggleButton>
      <ToggleButton value={'author'}>Author Space</ToggleButton>
      <ToggleButton value={'genre'}>Genre cluster</ToggleButton>
      <ToggleButton value={'search'}>Search Space</ToggleButton>
      <ToggleButton value={'network'}>User network</ToggleButton>
      
      <ToggleButton value={'recommendation'}>ML Prediction</ToggleButton>
      <ToggleButton value={'student'} >About</ToggleButton>
    </ToggleButtonGroup>
    
  </ButtonToolbar>
      </div>
  	</Toolbar>
	</AppBar>
	  </div>
	  {selected=='home'? <Home />: null}
	  {selected=='author'? <Author />: null}
	  {selected=='search'? <Search />: null}
	  {selected=='genre'? <Genre />: null}
	  {selected=='network'? <Network />: null}
	  {selected=='recommendation'? <Recommendation />: null}
    {selected=='student'? <Student />: null}
	  </div>
  );
}
}
export default App;
