
import React, { Component } from 'react';   
import Iframe from 'react-iframe'
import Card from 'react-bootstrap/Card';  
class Network extends Component {  
  componentDidMount() {  
     
  }  
  
   
  
  
  render() {  
    return (  
      <div style={{position:'absolute',top:50,bottom:0,right:0,left:0, overflow:'auto', padding:20,
    backgroundImage:'linear-gradient(to bottom right, #aa4b6b, #3b8d99)'}}>  
    <Card>
    <Card.Body>
    The visualization is a network with users (described by user_id) as nodes and the similarity between them as edges. 
    The similarity (edge) between two users is the number of books common to both users. 
    More the number of common books, the greater the similarity and hence is represented by a thicker line. 
    The tooltip on a user gives the list of all books associated with the user.
The network is constructed for a subset of data to present a distinctive visualization.
    </Card.Body>
    </Card>
    <br/>
      <Card>
      <Card.Header><h6>Questions</h6></Card.Header>
      <Card.Body>
      <ul>
      <li>What is the number of commom books between each pair of users?</li>
      <li>Which user has the most number of connections?</li>
      <li>How frequently does books appear in common books list?</li>
      <li>Which group of users have the most number of common books?</li>
      </ul>


      </Card.Body>
      </Card>
      <br/>
      <Card>
      <Card.Body>
      <Iframe url="https://public.flourish.studio/visualisation/1050741/embed"
        width="100%"
        height="500px"
        id="myId" overflow ="hidden"
        className="myClassname"
        display="initial"
        position="relative"/>
        </Card.Body>
        </Card>
       </div>  
    )  
  }  
}  
  
  
export default Network;  