class Home extends Component {  
  componentDidMount() {  
     
  }  
  
   
  
  
  render() {  
    return (  
      <div style={{position:'absolute',top:50,bottom:0,right:0,left:0, overflow:'auto', padding:30,
  			backgroundImage:`url('https://ae01.alicdn.com/kf/HTB1hLHiQXXXXXXyapXXq6xXFXXXu/custom-3d-wallpaper-Space-Wallpaper-Bookshelf-Background-Wall-living-3d-wallpaper-custom-3d-photo-wallpaper.jpg')`,
        backgroundSize: 'cover'}}>  
      <Card>
      <Card.Header><h6>Introduction</h6></Card.Header>
      <Card.Body>
      Goodreads is a social cataloging website where users can sign up and register books to generate library catalogs and reading lists. 
      This project investigates GoodReads data for books from various authors over a range of genre. 
      Analysis regarding the influence of author, genre and ratings for different users is performed using interative visualizations.
      <br /> <br/>
	In part 1, preprocessing steps such as analysis of data distributions, filtering, outlier detection and removal, 
	overview of features, top trends and search through subset of data was presented. 
	First round of questions regarding the structure and features of data, 
	trends with respect to provided features were answered with the help of various plots.
	Details of part 1 is briefly presented here.
	<br/> <br />
	This is extended to create a holistic website capable of visualizing user similarity, 
	review patterns, recommendations and searching through all properties of titles and authors.


      </Card.Body>
      </Card>
      <br /> <br />
      <Card>
      <Card.Header><h6>Dataset Source</h6></Card.Header>
      <Card.Body>

      For initial analysis in phase 1, the dataset was taken from the following link:<br/>
https://www.kaggle.com/jealousleopard/goodreadsbooks/download <br/><br/>
Further, individual datasets for user data, tags/shelf and goodread_ids is taken from Goodbooks-10k:<br/> https://github.com/zygmuntz/goodbooks-10k 
      </Card.Body>

      </Card>
      <br /> <br/>
       <Card>
      <Card.Header><h6>Dataset Cleaning and Processing</h6></Card.Header>
      <Card.Body>


     <b>Outlier detection</b><br/>
     
Let us look at the distribution of average_rating and ratings_count
As seen in the histogram below, the distribution is normal in the range 3 - 5, but there exists ratings of less than 1 which needs to be removed considering it as a default and irrelevant value.
<br/>
In case of ratings_count, the distribution is highly skewed. This clearly indicates the presence of outliers(very high values or low values). The case here might be having no ratings i.e. ratings_count = 0
<br/>
The third graph represnts a filtered ratings_count graph after removing of outliers. Now, the distribution is normal.
<div ><img src={Dist} style={{width:"100%", height:300}}></img></div> <br/> <br/>
      <b>What data columns need to be chosen?</b><br/>
      The dataset is such that there are redundant columns. But these serve different purposes.
      For example: ID
      There is 'id', 'book_id', 'goodread_ids', 'work_id' etc all used for identifying books.
      <br/>
      'id' here is a non-null field for representing book records
      <br/>
      'book_id' is used for combining multiple filters
      <br/>
      'goodread_ids' are used for linking external sources
      <br/>
      Hence, in each vizualization columns are used in accordance to their relevance, dependency and utility.
      <br/>
      <br/>
      <b>How are invalid values handled?</b>
      <br/>
      Invalid values may be null values or restricted values. 
      <br/>For records having invalid values to fields such as 'book_id' 
      or 'title' which needs to compulsorily exist are filtered while loading into the vizualization in 'tableau'
      <br/>
      There may be null values for goodread_ids or language_code or original title: Such records are retained.
      <br/>
      For publication year, the allowed 'years' are restricted in the visualizations.
      <br/>
      The dataset is highly normalized in the sense that each file has minimum number of columns and
      different files are joined with corresponsing user_id or book_ids.
      During joins, the 'left-outer' or 'right-outer' are selected based on the existance of ids and application.
      <br/>
      A few attributes such as average_rating needs to be computed using rating field and count.
      <br/>
      <br/>
      <b>What subset of data needs to be chosen?</b>
      <br/>
Shown below is the log plot of value counts i.e. number of books available in each language in the dataset. As seen, the records available are mostlty english : 'eng' and 'engUS'. 
All other records are filtered out for further analysis.
<br/>
In the visualizations initial filters are chosen to have only these records.
It can be changed by selecting the corresponding filters.
<br/>
<Iframe url="//plot.ly/~varshakr24/20.embed"
        width="100%"
        height="400px" frameBorder="2"
        id="myId" overflow ="hidden"
        className="myClassname"
        display="initial"
        position="relative"/>
      </Card.Body>

      </Card>
      <br /><br/>
      <Card>
      <Card.Header><h6>4 Cs of Data</h6></Card.Header>
      <Card.Body>
      <b>Completeness</b> <br/>
		After the DataCleaning phase, the data is complete in the sense that, data is available for all 10,000 books
		with required attributes such as 'book_id', 'title' and computed attributes such as average rating.
		<br/><br/>
		<b>Coherence</b><br/>
Thereâ€™s no mismatch on the datatype of any components in our data set with all invalid values like 'negative' or 'null'
removed in data cleaning. 
<br/><br/>
<b>Correctness</b><br/>
The data is fetched using good-reads api and normalized into multiple files with no external modifications 
for static values.
The correctness can also be verified by going to the corresponding good-reads links.
The computed values are seen to be closely matching the actual values from good-reads.
<br/><br/>
<b>Accountable</b><br />
The dataset is accessible to the public. The books data is not restricted.
<br/> The user information is not available due to privacy concerns. 
For occurance of any user related feature a unique user_id is used, including in the vizualizations,
only user_id can be seen and not 'name' or other information.  </Card.Body>
      </Card>
      <br /> <br/>
     
      
      <Card>
      <Card.Header><h6>Exploratory Analysis</h6></Card.Header>
      <Card.Body>
      <b> Book Statistics </b><br/>
     <b>What are the top rated books?</b>
<br/>
The plots below show top rated books:<br/>
Plot 1 shows top 10 high rated books in terms of average rating<br/>
Plot 2 shows top books in terms of highest ratings count and <br/>
Plot 3 shows top 10 book with highest number of text reviews <br/><br/>
<Iframe url="//plot.ly/~varshakr24/15.embed"
        width="100%"
        height="400px" frameBorder="2"
        id="myId" overflow ="hidden"
        className="myClassname"
        display="initial"
        position="relative"/>
<br/>
<br/>
<b>Author Statistics</b><br/>
<b>What are the properties of top rated authors in the current dataset?</b>
<br/>
The plots below show top authors in terms of various features.<br/>
Plot 1 shows authors with highest rating that is averaged out over all their books <br/>
Plot 2 shows authors with highest number of ratings count averaged over all their books <br />
Plot 3 shows authors with highest number of text reviews count <br />
Plot 4 shows authors with highest number of books <br />
Plot 5 shows authors with highest average number of pages in all their books <br />
<br/>
<Iframe url="//plot.ly/~varshakr24/4.embed"
        width="100%"
        height="820px" frameBorder="2"
        id="myId" overflow ="hidden"
        className="myClassname"
        display="initial"
        position="relative"/>
      
<br/>
<br/>
<b>Author Work Statistics</b>
<br/><b> How are the ratings and reviews distributed with respect to authors work?</b><br/>

The plot shown below describes various features of a record in a single graph.<br/>
The authors records are plotted as bubbles in the space of ratings count vs average rating.<br/>
The color of the bubble suggests the average number of pages in the books written by the author <br/>
The radius of the bubble corresponds to the number of books written by the author<br/>
(To obtain a clear visualization, plot is done on a subset of the dataset with lies near the mean of the bell curve)
<br/><br />
<Iframe url="//plot.ly/~varshakr24/1.embed"
        width="100%"
        height="500px" frameBorder="2"
        id="myId" overflow ="hidden"
        className="myClassname"
        display="initial"
        position="relative"/>
      </Card.Body>
      </Card>
<br/>
<br/>
<Card>
      <Card.Header><h6>Features of Website</h6></Card.Header>
      <Card.Body>

<b>Author Space</b><br/>
For any author or a group of authors, the user can understand the features in terms of rating, reviews or count.
With the relative change in size or color and the grouping with respect to authors, users can understand the influence of authors.

<br/><br/>

<b>Genre Cluster</b><br/>
The user can analyze books grouped by 'genre'. 
In any genre or group of genre the most rated books can be identified with interactive visualizations
<br/>
<br/>

<b>Search Space</b><br/>

Given a particular year/ genre / author, the most trending feature in terms of rating, reviews or count will be visualized, to answer questions regarding the most liked book, the most read book, the most popular author etc.
Any user can search for books in any particular shelf or 'tag'. Queries related to author, title, average rating, number of pages will be specified. Reviews, ratings are listed for each book. For each author, co-authors information, other books, average rating and interested genre will be made available. For each genre, trending authors and titles will be listed. Search based on user will also be made available.
<br/>
<br/>
<b>User Network</b><br/>

A network visualization of users with each user as vertex and number of common books as distance between vertices will be constructed. This answers questions about grouping of similar users, books existing in the group of similar users or books receiving common negative reviews.

<br/>
<br/>
<b>Recommendation System</b><br/>

All reviews given by users for different books are available in ratings.csv. 
A similarity score can be calculated between the current user and the users with submitted reviews. 
Out of the pool of available books with similarity in users, 
recommendations are made to any user selecting a book.
This is done using <b>Collaborative Filtering</b> technique.
<br/>
<br/>
<b> Integration with external sources</b><br/>

Links to Goodreads website can be constructed using the bookID. 
In any visualization, users can reach the goodreads site corresponsing to the book they are analyzing.


      </Card.Body>
      </Card>
      </div>  
    )  
  }  
}  
  
  
export default Home;  