
import React, { Component } from 'react';   
import Viz1 from './Viz1'; 
  import Card from 'react-bootstrap/Card';

class Author extends Component {  
  componentDidMount() {  
     
  }  
  
   
  
  
  render() {  
    return (  
      <div style={{position:'absolute',top:50,bottom:0,right:0,left:0, overflow:'auto', padding:20,
      backgroundImage:'linear-gradient(to bottom right, #aa4b6b, #3b8d99)'}}> 
      <div style={{display: 'flex',  justifyContent:'space-between', alignItems:'top'}}>
      <Card style={{width:'30%'}}>
      <Card.Body>
      The author space provides a consolidated view for authors and their works. 
      It also emphasizes the relative standing of book-author contrasted to other existing book-author information.
      
      The plot provides two levels of grouping: 
      <ol>
      <li>Each 'tile' corresponds to a book.</li>
      <li>Each 'grouped tile' corresponds to an author.</li>
      </ol>
      </Card.Body>
      </Card>
      <Card style={{width:'34%'}}>
      <Card.Body>
      There are two attributes associated with the tiles:<br/>
      <ol>
      <li>The color of a tile represents the average rating for a book.
      The rating-color map is shown at the bottom right of the plot.</li>
      <li>The size of a tile/grouped tile gives the number of ratings the item has received. 
      This is arranged in 
      the order of increasing grouped tile size which corresponds to the sum of all ratings for an author.</li>
      </ol>
      </Card.Body>
      </Card>
      <Card style={{width:'33%'}}>
      <Card.Body>
      The plot provides two kinds of filters:
      <ol>
      <li>From the plot: authors can be selected by selecting the tile. Multiple tiles (authors) can also be chosen. The visualization can be filtered to represent only for the chosen author/authors.</li>
      <li>Filters on the right of the plot: The plot can be filtered for publication year, author or language.</li>

      </ol>
      </Card.Body>
      </Card> 
      </div>
      <br/>
      <div>
      <Card style={{width:'100%'}}>
      
      <Card.Header><h6>Questions</h6></Card.Header>
      <Card.Body>
      <ul>
      <li>Trends:
      <ul>
      <li>Who are the most rated authors?</li>
      <li>Who are the best rated authors?</li>
      </ul>
      </li>
      <li>About single author:
      <ul>
      <li>How many books from the author?</li>
      <li>What are the available books from the author?</li>
      <li>Which are the most rated books?</li>
      <li>Which are the best rated books?</li>
      </ul>
      </li>
      <li>About multiple authors:
      <ul>
      <li>How can authors be compared in terms of mumber of books?</li>
      <li>How can authors be compared in terms of most ratings?</li>
      <li>How can authors be compared in terms of best ratings?</li>
      </ul>
      </li>
      </ul>


      </Card.Body>
      </Card>
      </div>
      <br/>
      <Card style={{width:'100%'}}>
      <Card.Body>
      <Viz1 />
      </Card.Body>
      </Card>
      </div>  
    )  
  }  
}  
  
  
export default Author;  