
import React, { Component } from 'react';   
import Correlation from './Correlation.PNG';
import Collaborative_filtering from './Collaborative_filtering.gif';
 import Card from 'react-bootstrap/Card';
 import Viz4 from './Viz4.js';
 import Mat from './matrix.png';


class Recommendation extends Component {  
  componentDidMount() {  
     
  }  
  
   
  
  
  render() {  
    return (  
      <div style={{position:'absolute',top:50,bottom:0,right:0,left:0, overflow:'auto', padding:15,
    backgroundImage:'linear-gradient(to bottom right, #aa4b6b, #3b8d99)'}}>
      
      <Card>
      <Card.Header> <h6>Recommendation System</h6></Card.Header>
      <Card.Body>
      Recommender systems are utilized in a variety of areas, 
      and are most commonly recognized as playlist generators for video and music services like Netflix, 
      YouTube and Spotify, product recommenders for services such as Amazon, or content recommenders for 
      social media platforms such as Facebook and Twitter
      
      <br/><br/>
      Two common approaches to building Recommendation systems are:<br/>
      <ul>
      <li>Content Based Filtering</li>
      <li>Collaborative filtering</li>
      </ul>
      Here, a recommender system is built for books using collaborative filtering using user-rating matrix for all books.
      </Card.Body>
      </Card>
      <br />
      
      <Card>
      <Card.Header> <h6>Content based filtering</h6></Card.Header>
      <Card.Body>Content-based filtering methods are based on a description of the item and a profile of the user's preferences.
      These methods are best suited to situations where there is known data or properties. 
      Content-based recommenders treat recommendation as a user-specific classification problem and learn a classifier 
      for the user's likes and dislikes based on product features.</Card.Body>
      </Card>
      <br />

      <Card>
      <Card.Header><h6> Collaborative filtering</h6></Card.Header>
      <Card.Body><div ><img src={Collaborative_filtering} style={{width:250, height:250, float:'left'}}></img>
       Collaborative filtering is a method of making automatic predictions (filtering) about the interests of a user 
       by collecting preferences or taste information from many users (collaborating). 
       The underlying assumption of the collaborative filtering approach is that if a person A 
       has the same opinion as a person B on an issue, 
       A is more likely to have B's opinion on a different issue than that of a randomly chosen person.
       <br/><br/>
       This image shows an example of predicting of the user's rating using collaborative filtering. 
       At first, people rate different items (like videos, images, games). 
       After that, the system is making predictions about user's rating for an item, 
       which the user hasn't rated yet. These predictions are built upon the existing ratings of other users, 
       who have similar ratings with the active user. 
       <br/>source: WikiPedia
       <br/><br/>
       For instance, in our case the system has made to make predictions based on the ratings given to books by different users.
       <br />
       
      </div>

      </Card.Body>
      </Card>
      <br />
      
      
      <Card>
      <Card.Header> <h6> Matrix factorization and Single Value Decomposition</h6></Card.Header>
      <Card.Body> <div >
      <img src={Mat} style={{width:600, height:250, float:'left' , margin:20, marginRight:40}}></img>
      <br/>
      Matrix factorization algorithms work by decomposing the user-item interaction matrix into the product of two lower dimensionality rectangular matrices.
      <br/><br/>The image summarizes the core idea behind matrix factorization. 
      Let there be matrix A with dimensionality of (m,n) this matrix can be viewed as a dot product between 
      two matrix with each matrices having dimensions of (m,k) and (k,n).</div>
      <br/><br/><br/><br/><br/><br/><br/>
      <div>
      <img src={Correlation} style={{width:750, height:350, float:'right', marginLeft:40}}></img>
      Singluar Value Decomposition is one way way of doing matrix-factorization.
      The number of features chosen for SVD is 10.
      With this, A correlation matrix is computed for each pair of books as shown in the image.
      <br/>
      This is a matrix of size 10,000*10,000
      <br/>
      <br/>
      Here, the correlation matrix is precomputed for use in the vizualization. 
      From the correlation matrix a list of pairwise correlations are extracted.
      This list is filtered to include only those values above threshold which is 0.9.

      </div>
      </Card.Body>
      </Card>
     	<br/>

     	<Card>
      <Card.Header> <h6> Recommendation System </h6></Card.Header>
      <Card.Body> <b>Usage Instructions:</b> <br/>

     A user has to enter any book that he/she likes in the provided textbox.
     The system calculates the correlation between the chosen book and all available books based on other users ratings.
     The recommendations are such that the correlation is greater than 0.9.
     The provided recommendations are ranked wrt correlation.
     <br/>
     <br/> Note: The chosen book text has to match the title exactly as in the database.
  
  	<br/><br/>
  	<Viz4 /></Card.Body>
      </Card>
     
      </div>  
    )  
  }  
}  
  
  
export default Recommendation;  